"use client"

import { useState } from "react"
import { Sidebar, SidebarContent, SidebarHeader, SidebarMenu, SidebarMenuItem, SidebarMenuButton, SidebarProvider, SidebarTrigger } from "@/components/ui/sidebar"
import { BarChart3, Home, TrendingUp, Calendar, Image, DollarSign, Target, Users, Clock, Globe, Lightbulb, MessageSquare, Settings, Brain, Building, Zap, Sparkles } from "lucide-react"
import { SocialMediaManager } from "./social-media-manager"
import { RedesignedOverviewDashboard } from "./redesigned-overview-dashboard"
import { AIPoweredBusinessDashboard } from "./index"

import { BrandAssetsHub } from "./brand-assets-hub"
import { EnhancedCashLog } from "./enhanced-cash-log"

import { EnhancedAudienceTracker } from "./enhanced-audience-tracker"
import { AIPoweredAdIdeaGenerator } from "./ai-powered-ad-idea-generator"
import { EnhancedAutoMessages } from "./enhanced-auto-messages"
import { AutoRepliesEngine } from "./auto-replies-engine"
import { BrandingPlaceholder } from "./branding-placeholder"
import { AIAudienceTimingInsights } from "./ai-audience-timing-insights"
import RageIntel from "./rageintel"
import AdPilotAI from "./adpilot-ai"
import { AIChat } from "./ai-chat"
import { WebsiteAnalytics } from "./website-analytics"
import { CRMSalesPipeline } from "./crm-sales-pipeline"
import { AIDropPredictor } from "./ai-drop-predictor"
import { AIROIForecastEngine } from "./ai-roi-forecast-engine"
import { AIStrategyComposer } from "./ai-strategy-composer"

const sidebarItems = [
  { id: "dashboard", label: "Social Media Manager", icon: Home },
  { id: "aibusinessdashboard", label: "AI Business Dashboard", icon: Brain },
  { id: "redesigneddashboard", label: "Redesigned Dashboard", icon: BarChart3 },
  { id: "brandassets", label: "Brand Assets Hub", icon: Image },
  { id: "cash", label: "Cash Log", icon: DollarSign },
  { id: "audience", label: "Audience Tracker", icon: Users },
  { id: "audiencetiming", label: "AI Audience Timing", icon: Clock },
  { id: "aiideas", label: "AI Ad Generator", icon: Brain },
  { id: "autoreplies", label: "Auto Replies", icon: MessageSquare },
  { id: "messages", label: "Auto Messages", icon: MessageSquare },
  { id: "branding", label: "Branding", icon: Settings },
  { id: "rageintel", label: "Competitor Tracking", icon: Brain },
  { id: "adpilot", label: "Ad Performance", icon: Target },
  { id: "aichat", label: "AI Chat & Collab", icon: MessageSquare },
  { id: "websiteanalytics", label: "Website Analytics", icon: BarChart3 },
  { id: "crm", label: "CRM & Sales", icon: Building },
  { id: "aidroppredictor", label: "AI Drop Predictor", icon: Zap },
  { id: "roiforecast", label: "AI ROI Forecast", icon: Target },
  { id: "strategycomposer", label: "AI Strategy Composer", icon: Sparkles },
]

export function EnhancedSocialMediaDashboard() {
  const [activeTab, setActiveTab] = useState("dashboard")

  const renderContent = () => {
    switch (activeTab) {
      case "dashboard":
        return <AIPoweredBusinessDashboard />
      case "social":
        return <SocialMediaManager />
      case "audience":
        return <EnhancedAudienceTracker />
      case "ads":
        return <EnhancedAdPerformance />
      case "auto":
        return <AutoRepliesEngine />
      case "messages":
        return <EnhancedAutoMessages />
      case "branding":
        return <BrandAssetsHub />
      case "cash":
        return <CashLog />
      case "timing":
        return <AIAudienceTiming />
      case "generator":
        return <AIPoweredAdIdeaGenerator />
      case "competitors":
        return <RageIntel />
      case "analytics":
        return <WebsiteAnalytics />
      case "crm":
        return <CRMAndSales />
      case "drops":
        return <Drops />
      case "forecast":
        return <AIROIForecastEngine />
      case "strategycomposer":
        return <AIStrategyComposer />
      default:
        return <AIPoweredBusinessDashboard />
    }
  }

  return (
    <SidebarProvider>
      <div className="flex h-screen w-full bg-gradient-to-br from-slate-900 via-purple-900/50 to-slate-900 gpu-accelerated">
        <Sidebar className="border-r border-white/10 gpu-accelerated">
          <SidebarHeader className="border-b border-white/10 p-6 bg-gradient-to-r from-slate-800/50 to-slate-900/50 backdrop-blur-xl gpu-accelerated">
            <div className="flex items-center gap-3">
              <div className="p-2 rounded-lg bg-gradient-to-r from-pink-500 to-purple-600 shadow-lg shadow-pink-500/25 gpu-accelerated">
                <BarChart3 className="h-6 w-6 text-white" />
              </div>
              <div>
                <span className="font-bold text-white text-lg">Essentials Enhanced</span>
                <div className="text-xs text-gray-300">AI-Powered Business OS</div>
              </div>
            </div>
          </SidebarHeader>
          <SidebarContent className="p-4">
            <SidebarMenu>
              {sidebarItems.map((item) => (
                <SidebarMenuItem key={item.id}>
                  <SidebarMenuButton
                    onClick={() => setActiveTab(item.id)}
                    isActive={activeTab === item.id}
                    className="w-full justify-start transition-all duration-200 gpu-accelerated"
                  >
                    <item.icon className="h-4 w-4" />
                    <span>{item.label}</span>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              ))}
            </SidebarMenu>
          </SidebarContent>
        </Sidebar>

        <div className="flex-1 flex flex-col overflow-hidden gpu-accelerated">
          <header className="border-b border-white/10 p-6 flex items-center justify-between bg-gradient-to-r from-slate-800/30 to-slate-900/30 backdrop-blur-xl gpu-accelerated">
            <div className="flex items-center gap-4">
              <SidebarTrigger className="bg-gradient-to-r from-pink-500 to-purple-600 text-white shadow-lg shadow-pink-500/25 hover:shadow-xl hover:shadow-pink-500/40 hover:scale-105 transition-all duration-200 gpu-accelerated" />
              <div>
                <h1 className="text-2xl font-bold text-white">
                  {sidebarItems.find(item => item.id === activeTab)?.label || "Dashboard"}
                </h1>
                <div className="text-sm text-gray-300">Premium AI-Powered Interface</div>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <div className="px-3 py-1 rounded-full bg-gradient-to-r from-pink-500 to-purple-600 text-white text-xs font-semibold shadow-lg shadow-pink-500/25 gpu-accelerated">
                Enhanced Mode
              </div>
            </div>
          </header>

          <main className="flex-1 overflow-auto p-6 bg-gradient-to-br from-slate-900/50 to-slate-800/50 gpu-accelerated">
            <div className="max-w-7xl mx-auto transition-all duration-200">
              {renderContent()}
            </div>
          </main>
        </div>
      </div>
    </SidebarProvider>
  )
}
